@import "../../style/helper/mixins";
@import "../../style/helper/vareable";

/*-------   Fonts   -------*/
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,800');

/*=======   MAIN      =======*/
html{
  min-height: 100%;
  display: table;
  width: 100%;
  height: 1px;
}

body {
  color: #fff;
  background: #000 url("../img/bg_img.jpg") no-repeat center;
  background-size: cover;
  font-family: 'Open Sans', sans-serif;
  font-weight: $light;
  font-size: $base-font-size;
  line-height: 1.2;
  display: table;
  min-height: 100%;
  width: 100%;
  height: 1px;
  &.no-scroll{
    overflow: hidden;
  }
  @include mq('m'){
    //font-size: 14px;
  }
}

.main-wrapper{
  overflow: hidden;
  display: table;
  min-height: 100%;
  height: 1px;
  width: 100%;
}


//.main-wrapper{
//  position: relative;
//  min-height: 100%;
//  height: 100%;
//}

.wrapper{
  min-width: 320px;
  max-width: $wrap+20 + px;
  padding: 0 10px;
  margin: auto;
  @include clr;
  @include mq('xl'){
    padding: 0 20px;
  }
  @include mq('s'){
    padding: 0 10px;
  }
}

a {
  color: #fff;
  text-decoration: none;
  transition: $hover_t;
  &:hover{
    transition: $hover_t;
    color: #fff;
    text-decoration: underline;
    text-decoration-style: dotted;
  }
}

.btn{
  background: transparent;
  color: #fff;
  width: 100%;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  transition: $hover_t;
  cursor: pointer;
  &:hover{
    transition: $hover_t;
    color: #fff;
    text-decoration: none;
  }
}

.small-title{
  font-size: 16px;
}


.ic{
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: middle;
  content: '';
}

@mixin ic() {
  display: inline-block;
  vertical-align: middle;
  content: '';
}


/*======= / MAIN end  =======*/


/*=======   MEDIA      =======*/

img, video {
  max-width: 100%;
  height: auto;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.video-container iframe, .video-container object, .video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*======= / MEDIA end  =======*/

